.AccountItem {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.AccountItem:hover{
    cursor: pointer;
}
.AccountItem img{
    border-radius: 100%;
    width: 35px;
    height: 35px;
    margin-left: 10px;
}
.AccountItem p{
    margin: 0;
}
.AccountItem:hover img{
    border: 4px solid #44ACDF;
}
.AccountItem:hover p {
    color: #44ACDF;
}
.DropdownList {
    background-color: #eeeeee;
    border: 0.5px solid #cccccc;
    box-shadow: 5px 5px 9px gray;
    width: 250px;
    position: absolute;
    top: 80px;
    right: 10px;
    padding: 0;
    display: none;
}
.DropdownList li{
    color: black;
    text-align: center;
}
.AccountInfo {
    display: flex;
    margin: 0;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid #cccccc;
}
.AccountInfo img {
     width: 75px;
     height: 75px;
     border-radius: 100%;
    margin-right: 10px;
 }
.AccountInfo p {
    color: black;
    margin: 0;
    text-align: center;
}

.AccountInfo p .Name{
    color: black;
    margin: 0;
}

.AccountInfo p .Index {
    color: black;
    margin: 0;
    font-size: 15px;
}
.AccountInfo p .Mail{
    color: black;
    margin: 0;
    font-size: 13px;
}
.Odjava:hover{
    color: red;
}