.DrawerToggle {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 5px 0;
    box-sizing: border-box;
    cursor: pointer;
    z-index: 2;
}

.DrawerToggle div {
    width: 90%;
    height: 3px;
    background-color: white;
}
.DrawerToggle div:nth-child(1) {
    margin-bottom: 7px;
}
.DrawerToggle div:nth-child(2) {
    margin-bottom: 7px;
}
@media (min-width: 1025px) {
    .DrawerToggle {
        display: none;
    }
}