.SideNav {
    background-color: #3A4E61;
    width:290px;
    max-width: 80%;
    height: 100%;
    z-index: 4;
    overflow-y: auto;
    position: fixed;
    top: 0;
    left: 0;
    transition: transform 0.3s ease-out;
}

.SideNav ul li{
    list-style-type: none;
    color: white;
    font-family: Roboto,"Helvetica Neue",Arial,"Noto Sans"
}
.ListView {
    padding: 0;
    display: flex;
    flex-direction: column;
}

@media (max-width: 1000px) {
    .SideNav {
        display: none;
    }
    .Closed {
        display: none;
    }
}

.Open {
    display: block;
}

