.Button {
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    margin-top: 20px;
    font-weight: bold;
    text-align: center;
}

.Button:first-of-type {
    /*margin-left: 0;*/
    /*padding-left: 0;*/
}

.Success {
    color: #5C9210;
}

.Danger {
    color: #944317;
}

.Bordered {
    border: 2px solid #cccccc;
}
.Button:disabled{
    color: #ccc;
}