.Grupe {
    border: 1px solid #eee;
    box-shadow: 5px 10px 18px darkgray;
    padding: 10px;
    background-color: white;
    width: 70%;
    min-height: inherit;
    height: 80vh;
    max-height: 80vh;
    overflow-y: scroll;
    box-sizing: border-box;
}
.GrupeItems {
    margin: 5px;
    padding: 0;
    background-color: #eee;
    text-align: center;
    display: flex;
}
.GrupeItem {
    padding: 10px;
    margin: 0;
    width: 100%;
    background-color: #eee;
    text-align: center;
    display: flex;
    justify-content: space-between;
}
.GrupeItem:hover{
    cursor: pointer;
    background-color: #dedcdc;
}
.NoGroups{
    width: 100%;
    min-height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}
.DeleteItem{
    padding: 10px 15px;
    margin-left: 5px;
}
.DeleteItem:hover{
    background-color: salmon;
    cursor: pointer;
}
.NoGroups p {
    margin: 0;
}
.SelectFilter {
    margin: 5px;
    display: flex;
    width: auto;
    padding: 2px;
    border: 1px solid #ccc;
    font: inherit;
    box-sizing: border-box;
    align-items: center;
    overflow: hidden;
}
.FilterInput {
    border: 0;
    padding: 5px;
    width: 100%;
}
.FilterInput:focus {
    outline: none;
    background-color: #ccc;
}
@media (max-width: 750px) {
    .Grupe{
        width: 100%;
    }
}