.MenuView {
    padding: 30px 0;
    border-bottom: 0.5px solid gray;
    border-top: 0.5px solid gray;
}
.MenuView li{
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
}

.MenuView li a{
    padding: 10px 20px;
    display: flex;
    align-items: center;
    color: white;
}
.MenuView li i{
    margin-right: 1rem;
}
.MenuView li .rotateIcon{
    position: absolute;
    right: 0;
    box-sizing: border-box;
    margin-right: 1.5rem;
}
.MenuView > li a:hover {
    background-color: #44ACDF;
}
