.NavbarMenu {
    background-color: #3A4E61;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
}

.NavbarList {
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 10px;
    height: 100%;
}
.NavbarList li{
    padding: 10px;
    font-size: 1.2rem;
    list-style-type: none;
    color: white;
}
.NavbarList > li:nth-child(1){
     padding: 10px;
     font-size: 1.2rem;
     list-style-type: none;
     color: white;
     flex-grow: 2;
 }
.NavbarList > li:nth-child(2){
    padding: 10px;
    font-size: 1.5rem;
    list-style-type: none;
    color: white;
    flex-grow: 1;
    font-weight: bolder;
}
.NavbarList > li:nth-child(3){
    padding: 10px;
    font-size: 1.2rem;
    list-style-type: none;
    color: white;
    flex-grow: 0;
}
@media (max-width: 999px) {
    .NavbarList > li:nth-child(1){
        padding: 10px;
        font-size: 1.2rem;
        list-style-type: none;
        color: white;
        flex-grow: 0;
    }
    .NavbarList > li:nth-child(2){
        padding: 10px;
        font-size: 1.5rem;
        list-style-type: none;
        color: white;
        flex-grow: 1;
        font-weight: bolder;
    }
    .NavbarList > li:nth-child(3){
        padding: 10px;
        font-size: 1.2rem;
        list-style-type: none;
        color: white;
        flex-grow: 0;
    }
}
@media (max-width: 550px) {
    .NavbarList > li:nth-child(2){
        padding: 10px;
        font-size: 1rem;
        list-style-type: none;
        color: white;
        flex-grow: 1;
        font-weight: bolder;
    }
    .NavbarList > li:nth-child(3) {
        padding: 10px;
        font-size: 1rem;
        list-style-type: none;
        color: white;
        flex-grow: 0;
    }
}