* {
  margin: 0;
  padding: 0;

}

#root {
  min-height: 70vh;
  height: 85vh;
}
*::-webkit-scrollbar{
  width: 10px;
  background-color: #F5F5F5;
}
*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

#style-4::-webkit-scrollbar-thumb {
  background-color: #000000;
  border: 2px solid #555555;
}