.SharableList{
    display: flex;
    align-items: center;
}
.SharableListItem {
    flex-grow: 1;
}

.SharableControlsDesktop {
    display: flex;
    flex-direction: column;
    width: 5%;
}
.SharableControlsMobile {
    display: flex;
    flex-direction: row;
}
.SharableControlsMobile button {
    margin: 5px;
    padding: 0 10px;
}

button.Disabled {
    color: #cccccc;
}
.SharableControlsDesktop button {
    margin: 5px;
}

@media (min-width: 1401px) {
    .SharableControlsMobile{
        display: none;
    }
}
@media (max-width: 1400px){
    .SharableControlsDesktop{
        display: none;
    }
    .SharableList {
        flex-direction: column;
    }
    .SharableListItem{
        width: 100%;
    }
}
