.Select {
    width: 100%;
    padding: 10px;
}
.SelectFilter {
    display: flex;
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-bottom: none;
    font: inherit;
    box-sizing: border-box;
    align-items: center;
}
.FilterInput {
    border: 0;
    padding: 5px;
    width: 100%;
}
.FilterInput:focus {
    outline: none;
    background-color: #ccc;
}
.SelectPlaceholder{
    display: flex;
    align-items: center;
    justify-content: space-between;
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    padding: 6px 10px;
    font: inherit;
    box-sizing: border-box;
    width: 100%;
}
.SelectPlaceholder:hover{
    cursor: pointer;
    background-color: #eeeeee;
}
.Label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}
.SelectList {
    outline: none;
    border: 1px solid #ccc;
    font: inherit;
    box-sizing: border-box;
    width: 100%;
    overflow-y: scroll;
}

.SelectList li{
    list-style-type: none;
    padding: 5px;
    height: 35px;
    overflow: hidden;
}
.SelectList li:hover{
    background-color: #ccc;
    cursor: pointer;
}
.SelectList li.active:hover{
    background-color: #314253;
    cursor: pointer;
}
.DisplayNone{
    display: none;
}
.active {
    background-color: #3A4E61;
    color: white;
}