.CollapsibleMenu {
    display: none;
    background-color: #314253;
}
.Dropdown a.active {
    background-color: #44ACDF;
}
.Dropdown a.active .CollapsibleMenu{
    display: block;
}
.CollapsibleMenu ul {
    padding: 0;
}
.CollapsibleMenu ul li a{
    padding: 10px 20px;
}
.CollapsibleMenu ul li a:hover{
    color: #cccccc;
    cursor: pointer;
}
