.Godine {
    border: 1px solid #eee;
    box-shadow: 5px 10px 18px darkgray;
    background-color: white;
    padding: 10px;
    width: 30%;
    display: flex;
    flex-direction: column;
}
.GodineItem {
    padding: 10px;
    font: inherit;
    margin: 5px;
    background-color: #eee;
}
.GodineItem:hover{
    cursor: pointer;
    background-color: #cccccc;
}

.Active{
    background-color: #3A4E61;
    color: white;
}
.Active:hover{
    background-color: #314253;
}
@media (max-width: 750px) {
    .Godine{
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
    }
}
@media (max-width: 550px) {
    .Godine{
        width: 100%;
        flex-direction: column;
        justify-content: space-between;
    }
}